export const columns = [
    {
        title:'Fecha',
        field:'date',
    },
    {
        title:'Descripcion',
        field:'description'
    },
    {
        title:'Monto',
        field:'amount',
        render: (rowData:any) => (
            rowData.isPaid 
            ? (
                <h4 style={{color:'#88cc6d'}}>PAGO</h4>    
            )
            : (
                <div>
                    <h5>Total:{rowData.amount.toLocaleString('en-US', {
                        style:'currency',
                        currency:'USD'
                    })}</h5>
                    <h5>Seña: <span style={{color:'#88cc6d'}}>{rowData.prepay.toLocaleString('en-US', {
                        style:'currency',
                        currency:'USD'
                    })}</span></h5>
                    <h5>A pagar: <span style={{color:'#f08080'}}>{rowData.topay ? rowData.topay.toLocaleString('en-US', {
                        style:'currency',
                        currency:'USD'
                    }) : 0}</span></h5>
                </div>
            )
        )
    },
    {
        title:'Nombre',
        field:'name'
    },
    {
        title:'Telefono',
        field:'phone'
    },
    {
        title:'Patente', 
        field:'plate'
    },
    {
        title:'Estado' ,
        field:'isComplete', 
        render: (rowData:any) => 
            rowData.isComplete 
                ? (<h4 style={{color:'#88cc6d'}}>Listo</h4>) 
                : (<h4 style={{color:'#f08080'}}>En proceso...</h4>)
    }
]