import List from './tramites/List'
import AddTramite from './tramites/modals/AddTramite'
import { useDispatch, useSelector } from 'react-redux'
import { uiOpenTramiteModal } from '../../../actions/ui'
import { useEffect } from 'react'
import {tramiteStartLoading } from '../../../actions/tramites'
import { IRootState } from '../../../interfaces/interfaces'
import Loading from '../../loading/Loading'

const Tramites = () => {
    const dispatch = useDispatch()
    const {loading} = useSelector((state:IRootState)=>state.tramite)
    const handleOpenModal = () => {
        dispatch(uiOpenTramiteModal())
    }

    useEffect(()=>{
        dispatch(tramiteStartLoading())
    },[dispatch])
    if(loading){
        return <Loading />
    }
    return (
        <section className="section center">
            <h2 className="section__title">Tramites</h2>
            <span className="tms__subtitle">En este apartado se encuentran los tramites discriminados por patente y fecha</span>
            <div className="button button__flex" onClick={handleOpenModal}>
                Agregar Tramite
                <i className="uil uil-clipboard-alt button__icon"></i>
            </div>
            <List />
            <AddTramite />
        </section>
    )
}

export default Tramites
