import { Modal } from '@material-ui/core';
import moment from 'moment';
import { useEffect, useState } from 'react';
import DateTimePicker from 'react-datetime-picker';
import { useDispatch, useSelector } from 'react-redux';
import { tramiteClearActive, tramiteStartAddNew, tramiteStartUpdate } from '../../../../../actions/tramites';
import { uiCloseTramiteModal } from '../../../../../actions/ui';
import { IRootState } from '../../../../../interfaces/interfaces';
import { addTramite, Links, Tramite } from '../../../../../types/types';

const now = moment().minutes(0).seconds(0);

const init = {
    amount:0,
    prepay:0,
    description:'', 
    phone:'', 
    date:now.toDate(), 
    name:'',
    plate:'',
    links:[]
}

const initlinks: Links[] = []

const initLinkValue = {
    name:'', 
    link:''
} 

const AddTramite = () => {
    const dispatch = useDispatch()
    
    const {modalTramiteOpen} = useSelector((state:IRootState) => state.ui)
    const {activeTramite} = useSelector((state:IRootState) => state.tramite)

    const [linkValue, setLinkValue] = useState(initLinkValue)

    const [formValues, setFormValues] = useState<addTramite>(init)
    const [dateValue, setDateValue] = useState(now.toDate())
    const [links, setLinks] = useState(initlinks);

    const [showLinks, setShowLinks] = useState(false)

    const {amount, prepay, description, phone, name, plate} = formValues
    
    const onCloseModal = () => {
        dispatch(uiCloseTramiteModal());
        dispatch(tramiteClearActive())
        setDateValue(now.toDate())
        setFormValues(init)
        setLinks([])
        setLinkValue(initLinkValue)
    }
    const handleDate = (e:Date) => {
        setDateValue(e)
        setFormValues({
            ...formValues,
            date:e
        })
    }
    const handleInputChange = (event:any) => {
        setFormValues({
            ...formValues,
            [event.target.name]:event.target.value
        })
    }
    const handleLinkChange = (event:any) => {
        setLinkValue({
            ...linkValue,
            [event.target.name]:event.target.value
        })
    }
    const handleSubmit = (e:any) => {
        e.preventDefault()
        if(activeTramite){
            const tramiteUpdate:Tramite = {
                id: activeTramite.id,
                date: dateValue,
                description,
                amount,
                prepay,
                name,
                phone,
                plate,
                links
            }
            dispatch(tramiteStartUpdate(tramiteUpdate))
        }else{
            dispatch(tramiteStartAddNew(formValues))
        }
        onCloseModal()
    }
    const handleRemoveLink = (item:any) => {
        const array = links.filter(link => link.name !== item.name)
        setLinks([...array])
    }
    const handleAddLink = (event:any) => {
        event.preventDefault()
        if(!!linkValue.name && !!linkValue.link){
            setLinks([...links, linkValue] )
            setFormValues({
                ...formValues,
                links:[...links, linkValue]
            })
            setLinkValue(initLinkValue)
        }
    }
    const handleShowLinks = (event:any) => {
        setShowLinks(!showLinks)
    }
    useEffect(() => {
        if(activeTramite){
            setFormValues(activeTramite)
            setDateValue(moment(activeTramite.date).toDate())
            setLinks(activeTramite.links)
            
        }else{
            setFormValues(init)
            setDateValue(now.toDate())
            setLinks([])
            setLinkValue(initLinkValue)
        }
    }, [activeTramite, setFormValues])

    return (
        <Modal
                open={modalTramiteOpen}
                onClose={onCloseModal}
                className="tms__modal"
        >
            <div className="tms__form__container">
                <h1 className="section__title">{activeTramite ? "Editar Tramite" : "Agregar Tramite"}</h1>
                <form className="tms__form grid">
                    <div className="tms__form__content">
                        <label className="tms__form__label">Fecha</label>
                        <div>
                            <DateTimePicker 
                                className="tms__form__datepicker"
                                onChange={handleDate}
                                value={dateValue}
                                format='dd-MM-y'
                            />
                        </div>
                    </div>
                    <div className="tms__form__inputs grid">
                        <div className="tms__form__content">
                            <label className="tms__form__label">Nombre</label>
                            <input 
                                name="name" 
                                type="text" 
                                className="tms__form__input"
                                autoComplete="off"
                                value={name}
                                onChange={handleInputChange}
                            />
                        </div>
                        <div className="tms__form__content">
                            <label className="tms__form__label">Patente</label>
                            <input 
                                type="text" 
                                className="tms__form__input"
                                value={plate}
                                onChange={handleInputChange}
                                name="plate"
                                autoComplete="off"
                            />
                        </div>
                        <div className="tms__form__content">
                            <label className="tms__form__label">Operacion</label>
                            <input 
                                type="text" 
                                className="tms__form__input"
                                value={description}
                                onChange={handleInputChange}
                                name="description"
                                autoComplete="off"
                            />
                        </div>
                        <div className="tms__form__content">
                            <label className="tms__form__label">Monto</label>
                            <input 
                                type="number" 
                                className="tms__form__input"
                                value={amount}
                                onChange={handleInputChange}
                                name="amount"
                                autoComplete="off"
                            />
                        </div>
                        <div className="tms__form__content">
                            <label className="tms__form__label">Seña</label>
                            <input 
                                type="number" 
                                className="tms__form__input"
                                value={prepay}
                                onChange={handleInputChange}
                                name="prepay"
                                autoComplete="off"
                            />
                        </div>
                        <div className="tms__form__content">
                            <label className="tms__form__label">Telefono</label>
                            <input 
                                type="text" 
                                className="tms__form__input"
                                value={phone}
                                onChange={handleInputChange}
                                name="phone"
                                autoComplete="off"
                            />
                        </div>
                    </div>
                    <div className="tms__form__content ">
                        <div className="tms__form__links__title">
                            <label className="tms__form__label">Documentos...</label>
                            <i className={
                                showLinks 
                                    ? 'uil uil-angle-up tms__links__icon__close'
                                    : 'uil uil-angle-down tms__links__icon__close'
                            }
                                onClick={handleShowLinks}
                            ></i>
                        </div>
                        <div className={
                                showLinks 
                                    ? 'content__addlink'
                                    : 'no_display'
                        }>
                            <div className="tms__form__addlink__container">
                                <div className="tms__form__addlink__content">
                                    <div className="tms__form__addlink">
                                        <label className="tms__form__label">Nombre</label>
                                        <input autoComplete="off" className="tms__form__input-addlink" onChange={handleLinkChange} name="name" value={linkValue.name}/>
                                    </div>
                                    <div className="tms__form__addlink">
                                        <label className="tms__form__label">Link</label>
                                        <input autoComplete="off" className="tms__form__input-addlink" onChange={handleLinkChange} name="link" value={linkValue.link}/>
                                    </div>
                                </div>
                                <div className="tms__form__addlink__button">
                                    <button className="button" onClick={handleAddLink}>Agregar</button>
                                </div>      
                            </div>
                            <div className="tms__form__link__container">
                                {
                                    links && 
                                    (
                                        links.map(item => 
                                            (
                                                <div className="tms__form__link" key={item.name}>
                                                    <a href={item.link} target="_blank" rel="noopener noreferrer">{item.name}</a>
                                                    <i className="uil uil-times" onClick={() => handleRemoveLink(item)}></i>
                                                </div>
                                            )
                                        )
                                    )
                                }
                            </div>
                        </div>
                    </div>
                    <div className="tms__form__btns">
                        <div>
                            <button className='button button__flex' onClick={handleSubmit}>CONFIRMAR</button>
                        </div>
                       <div>
                            <button className='button button__flex' onClick={onCloseModal}>CANCELAR</button>
                       </div>
                    </div>
                </form>
            </div>
        </Modal>
    )
}

export default AddTramite
