import { useRef } from "react"
import { useDispatch, useSelector } from "react-redux"
import { NavLink } from "react-router-dom"
import { startLogout } from "../../actions/auth"
import { IRootState } from "../../interfaces/interfaces"

const NavBar = () => {
    const dispatch = useDispatch()
    const {name} = useSelector( (state:IRootState)=>state.auth)
    const navMenu = useRef<HTMLDivElement>(null)
    const activeClass = (isActive:boolean) => (
        isActive 
            ? 'nav__link active__link'
            : 'nav__link'
    )

    const showMenu = () => {
        navMenu.current?.classList.add('show__menu');
    }

    const closeMenu = () => {
        navMenu.current?.classList.remove('show__menu');
    }

    const handleExit = () => {
        dispatch(startLogout())
    }
    return (
        <header className="nav__header" id="nav__header">
            <nav className="nav__nav container">
                <NavLink to="/" className="nav__logo">
                    {name.toUpperCase()}
                </NavLink>
                <div className="nav__menu" id="nav__menu" ref={navMenu}>
                    <ul className="nav__list grid">
                        <li className="nav__item">
                            <NavLink to="/home" className={({isActive})=>activeClass(isActive)}>
                                <i className="uil uil-estate nav__icon"></i>Home
                            </NavLink>
                        </li>
                        <li className="nav__item">
                            <NavLink to="/tramites" className={({isActive})=>activeClass(isActive)}>
                                <i className="uil uil-clipboard-alt nav__icon"></i>Tramites
                            </NavLink>
                        </li>
                        <li className="nav__item">
                            <NavLink to="/registros" className={({isActive})=>activeClass(isActive)}>
                                <i className="uil uil-file-graph nav__icon"></i>Vehiculos
                            </NavLink>
                        </li>
                    </ul>
                    <i className="uil uil-times nav__close" id="nav__close" onClick={closeMenu}></i>
                </div>
                <div className="nav__btns">
                    <div className="nav__toggle" id="nav__toggle" onClick={showMenu}>
                        <i className="uil uil-apps"></i>
                    </div>
                    <i className="uil uil-signout nav__exit" onClick={handleExit}></i>
                </div>
            </nav>
        </header>
    )
}

export default NavBar
