import MaterialTable from "@material-table/core"
import { AttachMoney, PriorityHigh } from "@material-ui/icons"
import moment from "moment"
import { useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import Swal from "sweetalert2"
import { tramitesDayStartLoading, tramiteSetActive, tramiteStartDelete, tramiteStartUpdate } from "../../../../actions/tramites"
import { uiOpenTramiteModal } from "../../../../actions/ui"
import { IRootState } from "../../../../interfaces/interfaces"
import { TramiteList } from "../../../../types/types"
import { columns } from "./columns"
import DateTimePicker from 'react-datetime-picker';
const now = moment().minutes(0).seconds(0);
const List = () => {

    const dispatch = useDispatch()

    const {tramitesPerDay, tramites} = useSelector((state:IRootState) => state.tramite)
    const [tramiteParsed, setTramiteParsed] = useState<TramiteList[]>([])
    const [date, setDate] = useState(now.toDate())
    const handleDate = (data : Date) => {
        setDate(data)
    }
    useEffect(()=> {
        dispatch(tramitesDayStartLoading(date))
    },[date, tramites, dispatch])
    useEffect(() => {
        const parseTramite:TramiteList[] = []
        tramitesPerDay.forEach((t) => {
            const tramite:TramiteList = {
                id: t.id,
                date: moment(t.date).format('DD-MM-YYYY').toString(),
                plate: t.plate,
                description: t.description,
                amount: t.amount,
                topay: t.topay,
                prepay: t.prepay,
                name: t.name,
                phone: t.phone,
                isComplete: t.isComplete,
                isImportant: t.isImportant,
                isPaid:t.isPaid
            }
            parseTramite.push(tramite)
        })
        setTramiteParsed(parseTramite)
    }, [tramitesPerDay])
    const handleEdit = (tramite:any) => {
        const tramiteFull = tramitesPerDay.filter(e => e.id === tramite.id)
        dispatch(tramiteSetActive(tramiteFull[0]))
        dispatch(uiOpenTramiteModal())
    }
    const handleDelete = (tramite:any) => {
        Swal.fire({
            title:'Are you sure?',
            text:'You wont be able to revert this!',
            icon:'warning',
            showCancelButton: true
        }).then((result)=>{
            if(result.isConfirmed){
                dispatch(tramiteStartDelete(tramite.id))
            }
        })
    }
    const handleDone = (tramite:any) => {
        const tramiteFull = tramitesPerDay.filter(e => e.id === tramite.id)
        tramiteFull[0].isComplete = !tramiteFull[0].isComplete
        dispatch(tramiteStartUpdate(tramiteFull[0]))
    }
    const handleImportant = (tramite:any) => {
        const tramiteFull = tramitesPerDay.filter(e => e.id === tramite.id)
        tramiteFull[0].isImportant = !tramiteFull[0].isImportant
        dispatch(tramiteStartUpdate(tramiteFull[0]))
    }
    const handleIsPaid = (tramite:any) => {        
        const tramiteFull = tramitesPerDay.filter(e => e.id === tramite.id)
        if(tramiteFull[0].amount - tramiteFull[0].prepay <= 0){
            Swal.fire('The tramite is paid!', 'You must modify the amount or prepay', 'warning')
        }else{
            tramiteFull[0].isPaid = !tramiteFull[0].isPaid
            dispatch(tramiteStartUpdate(tramiteFull[0]))
        }
    }
    return (
        <div className="tms__list__container">
            <div className="tms__list__date">
                <DateTimePicker format='dd-MM-y' onChange={handleDate} value={date}/> 
            </div>
            <div>
                <MaterialTable 
                    columns={columns}
                    data={tramiteParsed}
                    title=""
                    actions={[
                        {
                            icon:'check',
                            tooltip:'Terminado',
                            onClick: (event, rowData) => handleDone(rowData)
                        },
                        {
                            icon:'edit',
                            tooltip:'Editar',
                            onClick: (event, rowData) => handleEdit(rowData) 
                        },
                        {
                            icon:'delete',
                            tooltip:'Eliminar',
                            onClick: (event, rowData) => handleDelete(rowData)
                        },
                        {
                            icon:PriorityHigh,
                            tooltip:'Marcar como importante',
                            onClick: (event, rowData)=> handleImportant(rowData)
                        },
                        {
                            icon:AttachMoney,
                            tooltip:'Marcar como Pago',
                            onClick:(event, rowData) => handleIsPaid(rowData)
                        }
                    ]}
                    options={
                        {
                            actionsColumnIndex: -1, 
                            columnsButton:true, 
                            searchFieldAlignment:'left',
                            rowStyle: rowData => ({
                                backgroundColor: (rowData.isImportant) ? '#ffcd6c' : ''
                            }),
                            pageSize:20,
                        }
                    }
                    localization={{
                        header:{
                            actions:'Acciones'
                        },
                        toolbar:{
                            searchPlaceholder:'Buscar'
                        },
                        pagination:{
                            labelRowsSelect:'Filas',
                            firstAriaLabel:'Primer pagina',
                            firstTooltip:'Primer pagina',
                            previousAriaLabel:'Pagina anterior',
                            previousTooltip:'Pagina anterior',
                            nextAriaLabel:'Pagina siguiente',
                            nextTooltip:'Pagina siguiente',
                            lastAriaLabel:'Ultima pagina',
                            lastTooltip:'Ultima pagina'
                        }
                    }}
                />
            </div>
        </div>
    )
}

export default List
